.max-width-content {
  max-width: 1300px;
  margin: 0 auto;
}
.allcover {
  position: absolute;
  top: 0;
  right: 0;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.ct {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.cl {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.app-about .content-part {
  margin: 0 100px;
  padding: 10px;
  background: #ffffff;
  font-size: 14px;
}
.app-about .content-part .title {
  margin-bottom: 10px;
  padding-left: 10px;
  border-left: 4px solid #ff7519;
  font-weight: 600;
}
.app-about .content-part .content-p {
  padding-left: 14px;
}
.app-about .content-part .content-p .title-3 {
  font-weight: 500;
  margin-bottom: 4px;
}
.app-about .content-part .content-p p {
  padding-left: 10px;
}
.app-about .content-part .certificate-img {
  width: 400px;
  height: 200px;
}
.app-about .google-map iframe {
  width: 60%;
  height: 344px;
  border: none;
}
.app-about .google-map .address {
  width: 40%;
  margin-left: 20px;
  font-size: 16px;
}
.app-about .google-map .address p {
  margin-bottom: 10px;
}
.app-about .google-map .address .p-tel {
  color: #ff7519;
  font-weight: 500;
}
@media screen and (max-width: 768px) {
  .app-about .content-part .certificate-img {
    width: 200px;
  }
}



