.max-width-content {
  max-width: 1300px;
  margin: 0 auto;
}
.allcover {
  position: absolute;
  top: 0;
  right: 0;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.ct {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.cl {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.app-home .slider-img {
  width: 100%;
  height: 500px;
}
.app-home .slick-dots {
  margin-bottom: 40px;
}
.app-home .slick-dots li button:before {
  color: #fab07b;
}
.app-home .slick-dots li.slick-active button:before {
  color: #ff7519;
}
.app-home .slick-prev {
  left: 20px;
  z-index: 222;
}
.app-home .slick-next {
  right: 20px;
}
.app-home .part-section {
  margin: 0 100px 10px;
  padding: 0 20px 10px;
  font-size: 16px;
  background: #ffffff;
}
.app-home .part-section .title {
  padding: 10px 0;
  color: #ff7519;
  text-align: center;
}
.app-home .part-1 .p-text {
  margin-bottom: 5px;
}
.app-home .part-2 .li-item {
  width: 25%;
  padding: 0 10px;
}
.app-home .part-2 .li-item .img-box {
  width: 100%;
  height: 0px;
  padding-bottom: 140%;
  overflow: hidden;
  background: #000000;
}
.app-home .part-2 .li-item img {
  width: 100%;
}
.app-home .part-2 .li-item .h3-title {
  padding: 10px 0;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.app-home .part-3 {
  height: 250px;
  padding: 0;
}
.app-home .part-3 .content {
  width: 100%;
  height: 100%;
  background: url('https://user-images.githubusercontent.com/35425986/49698882-f2e59100-fc04-11e8-9fba-c531586ffcd7.jpg') center no-repeat;
  background-size: 100%;
}
.app-home .part-3 .content-c {
  background: rgba(0, 0, 0, 0.2);
}
.app-home .part-3 .li-item {
  width: 100%;
  height: 140px;
  margin-top: 60px;
  background: rgba(0, 0, 0, 0.4);
  color: #eeecec;
}
.app-home .part-3 .li-item img {
  width: 50px;
  height: 50px;
  border-radius: 50px;
}
.app-home .part-3 .li-item a {
  color: #eeecec;
}
.app-home .part-4 {
  height: 250px;
  padding: 0;
  color: #eeecec;
}
.app-home .part-4 .content {
  width: 100%;
  height: 100%;
  background: url('https://user-images.githubusercontent.com/35425986/49698881-f24cfa80-fc04-11e8-943c-c625bc35113a.png') center no-repeat;
  background-size: 100%;
}
.app-home .part-4 .content-c {
  background: rgba(0, 0, 0, 0.2);
}
.app-home .part-4 .p-text {
  padding: 20px;
  text-align: center;
}
.app-home .part-4 .labels .label-item {
  display: inline-block;
  background: rgba(190, 139, 81, 0.6);
  line-height: 1;
  padding: 2px 6px;
  border-radius: 4px;
  margin: 10px;
}
.app-home .google-map iframe {
  width: 100%;
  height: 344px;
  border: none;
}
@media screen and (max-width: 768px) {
  .app-home .part-section {
    margin: 0 10px 10px;
    padding: 0 20px 10px;
  }
  .app-home .part-2 .li-item {
    padding: 0 5px;
  }
  .app-home .part-2 .li-item img {
    height: 270px;
  }
}



